@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&family=Roboto:wght@300;400&display=swap);
/* Importing Google Fonts */

/* Define primary color variables */
:root {
	--primary-400: #1f1f25;
	--primary-500: #292a31;
	--primary-600: #3c3e44;
}



/* Dark theme */
:root:has(.app.dark),
.app.dark {
	--primary-400: #1f1f25;
	--primary-500: #292a31;
	--primary-600: #3c3e44;
}


/* General styling */
.app {
	background-color: #1f1f25;
	background-color: var(--primary-400);
	isolation: isolate;
}

/* Resetting default styles */
*,
*::after,
*::before {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	transition: color 0.5s ease-in, background 0.5s ease-in, background-color 0.5s ease-in;
	-webkit-tap-highlight-color: transparent;
}



/* Custom scrollbar styles */
*::-webkit-scrollbar {
	width: 10px;
}
*::-webkit-scrollbar-track {
	background-color: #3c3e44;
}
*::-webkit-scrollbar-thumb {
	border: 2px solid #3c3e44;
	border-radius: 100vmax;
	background-color: #1f1f25;
}

/* Z-index utility */
.topZ {
	z-index: 99 !important;
}

/* Root styles */
:root {
	font-size: calc(1rem - 2px);
	color: white;
}

/* Image styles */
img {
	max-width: 100%;
	display: block;
}

/* Button, input, textarea styles */
button,
input,
textarea {
	border: none;
	outline: none;
	font-family: inherit;
	background: none;
}

/* Button and anchor styles */
button,
a {
	cursor: pointer;
	text-decoration: none;
	color: inherit;
}

/* Round image utility */
.roundimage {
	aspect-ratio: 1;
	border-radius: 50%;
	object-fit: cover;
}

/* Container styles */
.container {
	max-width: 1600px;
	min-height: 100vh;
	padding-inline: 2rem;
	margin-inline: auto;
	display: flex;
	flex-direction: column;
	padding-bottom: 2rem;
}

/* Disabled button style */
button:disabled {
	cursor: not-allowed;
}

/* Placeholder style for inputs */
input::-webkit-input-placeholder {
	font-size: 0.9rem;
}
input::placeholder {
	font-size: 0.9rem;
}

/* Green class with specific styles */
.green {
	position: relative;
}

/* Sidebar styles */
.sidebar {
	position: fixed;
	display: none;
	opacity: 0;
	z-index: 999;
	border-radius: 10px;
	top: 100px;
	bottom: 10px;
	left: 0;
	right: 1rem;
	-webkit-transform: translateX(-20%);
	        transform: translateX(-20%);
	pointer-events: none;
}

/* Green class pseudo-element */
.green::after {
	content: "";
	position: absolute;
	width: 30%;
	aspect-ratio: 1;
	background-color: rgb(21, 214, 31);
	border-radius: 50%;
	right: 0;
	bottom: 0;
}

/* Gradient border utility */
.gradient-border {
	position: relative;
}

/* Gradient border pseudo-element */
.gradient-border::before {
	content: "";
	position: absolute;
	inset: -2px;
	border-radius: inherit;
	z-index: -1;
	background: linear-gradient(145deg, #3c3e44, transparent);
	background: linear-gradient(145deg, var(--primary-600), transparent);
}

/* Media queries */
@media (max-width: 1000px) {
	.container {
		padding-inline: 0.5rem;
	}
}

@media (max-width: 800px) {
	.container {
		max-width: 500px;
	}
	.sidebar {
		display: block;
		visibility: hidden;
		transition: all 0.2s linear;
	}
	.visible {
		-webkit-transform: translateX(0);
		        transform: translateX(0);
		visibility: visible;
		opacity: 1;
		pointer-events: all;
		box-shadow: 0 10px 100px rgb(0, 0, 0);
		transition: opacity 0.2s linear, -webkit-transform 0.2s linear;
		transition: opacity 0.2s linear, transform 0.2s linear;
		transition: opacity 0.2s linear, transform 0.2s linear, -webkit-transform 0.2s linear;
	}
}

/* Email input and datalist styles */
.email {
	position: relative;
}

.email-datalist {
	position: absolute;
	background-color: #1f1f25;
	background-color: var(--primary-400);
	display: grid;
	border-radius: 10px;
	inset: 80% 0 auto 0;
	max-height: 12rem;
	overflow: auto;
}

.email-datalist option {
	padding-inline: 1rem;
	display: block;
	height: 3rem;
	display: grid;
	align-items: center;
	cursor: pointer;
	transition: background-color 0.2s linear;
	font-size: 0.8rem;
}

.email-datalist option:hover {
	background-color: #3c3e44;
	background-color: var(--primary-600);
}

/* Loading box styles */
.loadingBox {
	position: relative;
	background-color: #3c3e44;
	background-color: var(--primary-600);
	overflow: hidden;
}

.loadingBox::after {
	position: absolute;
	content: "";
	height: 500%;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	width: 30px;
	background: #1f1f25;
	background: var(--primary-400);
	-webkit-filter: blur(50px);
	        filter: blur(50px);
	left: -50%;
	-webkit-animation: loading 1s linear infinite;
	        animation: loading 1s linear infinite;
}

/* Loading animation keyframes */
@-webkit-keyframes loading {
	to {
		left: 150%;
	}
}
@keyframes loading {
	to {
		left: 150%;
	}
}

