	padding: 0.75rem 1rem;
	color: white;
	border-radius: 10px;
	margin-bottom: 2rem;
}

.auth button {
	background: var(--primary-400);
	width: 100%;
	color: white;
	padding: 0.75rem;
	border-radius: 10px;
	font-size: 1rem;
}

.auth p span:hover {
	cursor: pointer;
	text-decoration: underline;
}

@media (max-width: 420px) {
