.backdrop {
	position: fixed;
	inset: 0;
	display: grid;
	visibility: hidden;
	background-color: rgba(0, 0, 0, 0.544);
	place-content: center;
	z-index: 99;
	opacity: 0;
	-webkit-backdrop-filter: blur(0);
	        backdrop-filter: blur(0);
	pointer-events: none;
	transition: opacity 0.2s linear, visibility 0.2s linear, -webkit-backdrop-filter 0.2s linear;
	transition: opacity 0.2s linear, backdrop-filter 0.2s linear, visibility 0.2s linear;
	transition: opacity 0.2s linear, backdrop-filter 0.2s linear, visibility 0.2s linear, -webkit-backdrop-filter 0.2s linear;
}

.backdrop.show {
	opacity: 1;
	visibility: visible;
	pointer-events: all;
	-webkit-backdrop-filter: blur(0.3rem);
	        backdrop-filter: blur(0.3rem);
}

.backdrop.show .backdrop__content {
	-webkit-transform: translateY(0);
	        transform: translateY(0);
	opacity: 1;
}

.backdrop__content {
	border-radius: 10px;
	-webkit-transform: translateY(-30px);
	        transform: translateY(-30px);
	opacity: 0;
	transition: opacity 0.2s ease-out, -webkit-transform 0.3s ease-out;
	transition: transform 0.3s ease-out, opacity 0.2s ease-out;
	transition: transform 0.3s ease-out, opacity 0.2s ease-out, -webkit-transform 0.3s ease-out;
	margin: 1rem;
}


